import React, { useCallback, useEffect, useMemo, useState } from 'react';
import useFoodWasteTable from '../../hooks/useFoodWasteTable';
import { getWeekDateRange } from '../../utils/date';
import Toast from '../../components/Toast';
import Button from '../../components/Button';

const WEEKDAYS = ["Monday", "Tuesday", "Wednesday", "Thursday", "Friday"];
const NUMERIC_FIELD_KEYS = new Set(["number_of_users", "food_waste", "total_waste"]);

export function links() {
    return [
        { rel: "stylesheet", href: "https://cdn.jsdelivr.net/npm/@glideapps/glide-data-grid/dist/index.css" },
    ];
}

function toIsoDateString(date) {
    return date.toISOString().slice(0, 10);
}

function parseDateFromRecord(dateValue) {
    if (!dateValue) return null;
    if (typeof dateValue === "string" && dateValue.length >= 10) {
        return dateValue.slice(0, 10);
    }
    const parsed = new Date(dateValue);
    if (Number.isNaN(parsed.getTime())) return null;
    return toIsoDateString(parsed);
}

function getIsoDateForWeekday(weekNumber, weekdayOffsetFromMonday, year) {
    const firstDayOfYear = new Date(year, 0, 1);
    const daysOffset = (weekNumber - 1) * 7;
    const startDate = new Date(firstDayOfYear.getTime() + daysOffset * 86400000);
    const day = startDate.getDay();
    const diff = (day === 0 ? -6 : 1) - day;
    startDate.setDate(startDate.getDate() + diff + weekdayOffsetFromMonday);
    return toIsoDateString(startDate);
}

function formatWeekdayLabel(dateIso) {
    return new Date(dateIso).toLocaleDateString("en-GB", {
        weekday: "long",
        day: "2-digit",
        month: "2-digit",
        year: "numeric"
    });
}

function coerceDisplayValue(value) {
    return value === null || value === undefined ? "" : value;
}

function FoodWaste() {
    const {
        week,
        setWeek,
        filteredRecords,
        handleUpdate,
        getPendingValue,
        saveUpdates,
        loading,
        isSaving,
        hasPendingUpdates,
        toast,
        dismissToast
    } = useFoodWasteTable();

    const [isClient, setIsClient] = useState(false);
    const [DataEditor, setDataEditor] = useState(null);

    useEffect(() => {
        setIsClient(true);
    }, []);

    useEffect(() => {
        let mounted = true;
        import('@glideapps/glide-data-grid').then((mod) => {
            if (!mounted) return;
            setDataEditor(() => mod.default || mod.DataEditor || null);
        }).catch(() => {
            // no-op
        });
        return () => { mounted = false; };
    }, []);

    const { start, end } = getWeekDateRange(week);

    const referenceYear = useMemo(() => {
        if (filteredRecords.length > 0) {
            const iso = parseDateFromRecord(filteredRecords[0].date);
            if (iso) {
                const parsed = new Date(iso);
                if (!Number.isNaN(parsed.getTime())) {
                    return parsed.getFullYear();
                }
            }
        }
        return new Date().getFullYear();
    }, [filteredRecords]);

    const recordsByDate = useMemo(() => {
        const map = new Map();
        filteredRecords.forEach((item) => {
            const iso = parseDateFromRecord(item.date);
            if (iso) {
                map.set(iso, item);
            }
        });
        return map;
    }, [filteredRecords]);

    const rows = useMemo(() => {
        return WEEKDAYS.map((_, idx) => {
            const dateIso = getIsoDateForWeekday(Number(week), idx, referenceYear);
            const record = recordsByDate.get(dateIso);
            const key = record?.id ?? dateIso;

            return {
                key,
                date: dateIso,
                weekDay: formatWeekdayLabel(dateIso),
                number_of_users: coerceDisplayValue(getPendingValue(key, "number_of_users", record?.number_of_users ?? "")),
                food_waste: coerceDisplayValue(getPendingValue(key, "food_waste", record?.food_waste ?? "")),
                total_waste: coerceDisplayValue(getPendingValue(key, "total_waste", record?.total_waste ?? "")),
            };
        });
    }, [week, referenceYear, recordsByDate, getPendingValue]);

    const columns = useMemo(() => {
        return [
            { id: "weekDay", title: "Week Day" },
            { id: "number_of_users", title: "Users" },
            { id: "food_waste", title: "Food Waste (g)" },
            { id: "total_waste", title: "Total Waste (g)" },
        ];
    }, []);

    const getCellContent = useCallback((cell) => {
        const [col, row] = cell;
        const rowModel = rows[row];
        const colId = columns[col].id;
        const value = rowModel[colId];
        if (colId === "weekDay") {
            return {
                kind: "text",
                allowOverlay: false,
                displayData: String(value || ""),
                data: String(value || ""),
            };
        }
        return {
            kind: "number",
            displayData: value === "" ? "" : String(value),
            data: value === "" ? undefined : Number(value),
            allowOverlay: true,
        };
    }, [rows, columns]);

    const onCellEdited = useCallback((cell, newValue) => {
        const [col, row] = cell;
        const rowModel = rows[row];
        const colId = columns[col].id;
        if (!NUMERIC_FIELD_KEYS.has(colId)) return;
        let normalized = null;
        if (newValue?.kind === "number") {
            const n = Number(newValue.data);
            normalized = Number.isNaN(n) ? null : n;
        } else if (newValue?.kind === "text") {
            const n = Number(newValue.data);
            normalized = newValue.data === "" || Number.isNaN(n) ? null : n;
        }
        handleUpdate(rowModel.key, { date: rowModel.date, [colId]: normalized });
    }, [rows, columns, handleUpdate]);

    return (
        <div className="p-6">
            <div className="flex justify-between gap-4 mb-4">
                <div className="flex gap-4 items-center">
                    <h3 className="font-semibold">
                        <span className='text-[var(--primary-color)] text-2xl'>Week {week} /</span> {start} - {end}
                    </h3>
                    <div className="flex gap-2">
                        <button
                            onClick={() => setWeek((prev) => Math.max(prev - 1, 1))}
                            className="px-3 py-1 border rounded hover:bg-gray-100"
                        >
                            ←
                        </button>

                        <button
                            onClick={() => setWeek((prev) => prev + 1)}
                            className="px-3 py-1 border rounded hover:bg-gray-100"
                        >
                            →
                        </button>
                    </div>
                </div>


                <div className="flex justify-end items-center mb-6 pos-top">
                    <p className="mr-4">Remember to update before leaving</p>
                    <Button onClick={saveUpdates} variant="primary" disabled={isSaving || !hasPendingUpdates}>
                        Update
                    </Button>
                </div>
            </div>

            {toast && (
                <Toast
                    message={toast.message}
                    type={toast.type}
                    onClose={dismissToast}
                />
            )}

            {loading ? (
                <p>Loading…</p>
            ) : isClient && DataEditor ? (
                <div style={{ height: '340px', width: '100%' }}>
                    <DataEditor
                        columns={columns}
                        rows={rows.length}
                        getCellContent={getCellContent}
                        onCellEdited={onCellEdited}
                        smoothScrollX={true}
                        smoothScrollY={true}
                        freezeColumns={1}
                        rowMarkers="none"
                    />
                </div>
            ) : null}
        </div>
    );
}

export default FoodWaste;
